import React, { Component, Suspense } from 'react';
import DisplayCode from '../../../PublishCommonComponents/DisplayCode';
import { UGC_SUITE_WALL_PREFIX } from '../../../../../Services/Api'
class RaydiantCode extends Component {

    render() {
        const { data } = this.props
        return <div className="card text-decoration-none">
            <div className="card-body p-lg-7">
                <div className="m-auto">
                    <div className="embed_code">
                        <h5 className="mb-5 fw-bolder">Steps To Display Social wall With Raydiant</h5>
                        <h6>Step 1 : Collect The Generated Social Wall Display Code</h6>
                        <DisplayCode displayCode={data && Object.keys(data).length > 0 ? `${UGC_SUITE_WALL_PREFIX}${data.wallId}` : null} />
                        <h6 className="mb-5">Step 2: Add The Social Wall To Raydiant App In The Following Way</h6>
                        <ul className="list-unstyled list-points fs-7">
                            <li className="mb-3">Login to Raydiant dashboard and navigate to the <span className="fw-bold">Library</span></li>
                            <li className="mb-3">Click on + <span className="fw-bold">Create</span> button and choose <span className="fw-bold">Tagbox Display</span> from the available options</li>
                            <li className="mb-3">Now, paste the copied code into the <span className="fw-bold">Display code</span> field and click on <span className="fw-bold">Save</span></li>
                            <li className="mb-3">Congratulations, youâ€™ve successfully added the social wall in Raydiant.</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    }
}

export default RaydiantCode;